import React, { useState } from 'react';
import Tittle from '../Components/Tittle';
import Categories from '../Components/Categories';
import Menu from '../Components/Menu';
import Portfolio from '../Components/Portfolio';


const allCategories = ['All', ...new Set(Portfolio.map(item => item.category))];


const PortfolioPage = () => {
    const [categories, setCategories] = useState(allCategories);
    const [menuItems, setMenuItems] = useState(Portfolio);

    const filter = (category) => {
        if (category === 'All') {
            setMenuItems(Portfolio)
            return;
        }
        const filterData = Portfolio.filter((item) => {
            return item.category === category;
        });
        setMenuItems(filterData);
    }
    return (
        <div className="portfolioPage">
            <div className="title">
                <Tittle tittle={'Portfolios'} span={'Portfolios'} />
            </div>
            <div className="portfolios">
                <Categories filter={filter} categories={categories} />
                <Menu menuItem={menuItems} />
            </div>
        </div>
    )
}

export default PortfolioPage;

